.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 7rem;
}

.contentContainer {
  width: 90%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 3rem;
}

.contentContainer a:hover {
  color: blue !important;
}

.MBgroup {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.MBgroupColumn {
  margin: 0 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  flex: 1;
}

.MBgroup img {
  width: 100%;
  margin-bottom: 1rem;
}

.MBgroup h6 {
  font-family: "Avenir";
  font-style: normal;
  font-weight: 100;
  font-size: 17px;
  line-height: 28px;
}

.contentContainer1 {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 3rem;
  align-items: flex-start;
}

.subSections1 {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 2rem;
  width: 45%;
}

.subSections1 img {
  width: 100%;
  margin-top: 20px;
}

.subSections2 {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 2rem;
  width: 55%;
}

.wireframes {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.wireframes img {
  margin: 10px;
  width: 45%;
  margin-top: 30px;
}

.section {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 2rem;
}

.sectionHeader {
  font-family: "Avenir";
  font-style: normal;
  font-weight: 900;
  font-size: 35px;
  line-height: 55px;
  color: #2f76b5;
  margin-top: 15px;
}

.link {
  text-decoration: under;
  color: black;
}

.alert-enter-active {
  opacity: 1;
  transform: translateX(0);
  transition: opacity 300ms, transform 300ms;
}

.alert-exit {
  opacity: 1;
}

.alert-exit-active {
  opacity: 0;
  transform: scale(0.9);
  transition: opacity 300ms, transform 300ms;
}

@media (max-width: 768px) {
  .sectionHeader {
    font-size: 1.8rem;
  }
}
